@import "https://cdn.jsdelivr.net/npm/reset-css@5.0.1/reset.min.css";

* {
  box-sizing: border-box;
}

input {
  all: unset;

  box-sizing: border-box;

  appearance: none;
}

body {
  background-color: #fffbeb;

  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;

  font-size: 14px;

  /* color: #ad7be9; */
}

button {
  background-color: white;

  color: black;
}

a {
  text-decoration: none;

  color: inherit;
}

form {
  width: 100%;
}

.jweet-input::placeholder {
  color: white;

  opacity: 0.9;
}

.container {
  width: 100%;

  max-width: 320px;

  display: flex;

  flex-direction: column;
}

.jweet-container {
  padding: 30px 0 30px 0;
  background-color: #fffbeb;
}

.formInput {
  width: 100%;

  padding: 10px 20px;

  border-radius: 20px;

  border: 1px solid black;

  text-align: center;

  background-color: white;

  color: black;
}

.formBtn {
  cursor: pointer;

  width: 100%;

  padding: 7px 20px;

  text-align: center;

  color: white;

  border-radius: 20px;

  background-color: #04aaff;

  cursor: pointer;
}

.cancelBtn {
  cursor: pointer;

  background-color: tomato;
}

.nav-container {
  width: 100%;
  height: 100px;
  padding-top: 30px;
  background-color: #53abee;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2;
}
/* Profile Page */

.profile_Container {
  display: flex;

  flex-direction: column;

  height: 100vh;

  justify-content: center;

  align-items: center;
}

.profileForm {
  border-bottom: 1px solid rgba(255, 255, 255, 0.9);

  padding-bottom: 30px;

  width: 100%;

  display: flex;

  flex-direction: column;
}

.profile-link,
.home-link {
  margin: 10px 0 0 10px;
  color: white;
  float: left;
  display: flex;
  align-items: center;
}

.logOut {
  margin-top: 50px;
}

/* Nwitter Factory */

.factoryForm {
  display: flex;

  flex-direction: column;

  align-items: center;

  width: 100%;
}

.factoryInput__container {
  display: flex;

  justify-content: space-between;

  align-items: center;

  flex-wrap: wrap;

  position: relative;

  margin-bottom: 20px;

  width: 100%;
}

.factoryInput__input {
  flex-grow: 1;

  height: 40px;

  padding: 0px 20px;

  color: black;

  border: 1px solid #04aaff;

  border-radius: 20px;

  font-weight: 400;

  font-size: 12px;
}

.factoryInput__arrow {
  position: absolute;

  right: 0;

  background-color: #04aaff;

  height: 40px;

  width: 40px;

  padding: 10px 0px;

  text-align: center;

  border-radius: 20px;

  color: white;
}

.factoryInput__label {
  color: #04aaff;

  cursor: pointer;
}

.factoryInput__label span {
  margin-right: 10px;

  font-size: 12px;
}

.factoryForm__attachment {
  display: flex;

  flex-direction: column;

  justify-content: center;
}

.factoryForm__attachment img {
  height: 80px;

  width: 80px;

  border-radius: 40px;
}

.file-input {
  display: none;
}

.factoryForm__clear {
  color: #04aaff;

  cursor: pointer;

  text-align: center;
}

.factoryForm__clear span {
  margin-right: 10px;

  font-size: 15px;
}

/* jweet */

.jweetEdit .formBtn {
  cursor: pointer;

  margin-top: 15px;

  margin-bottom: 5px;
}

.jweet {
  position: relative;
  min-height: 70px;
  margin: 20px 10px 0 10px;
  padding: 15px;
  background-color: white;
  /* border-radius: 30px; */
  /* box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px; */

  /* margin-bottom: 20px;

  background-color: white;

  width: 100%;

  max-width: 320px;

  padding: 20px;

  border-radius: 10px;

  position: relative;

  display: flex;

  flex-direction: column;

  color: rgba(0, 0, 0, 0.8); */
}

.username {
  font-weight: bold;
  font-size: 18px;
  color: #53abee;
}

.jweet h4 {
  font-size: 10px;
}

.jweet img {
  right: -10px;

  top: 20px;

  position: absolute;

  border-radius: 50%;

  width: 50px;

  height: 50px;

  margin-top: 10px;
}

.jweet__actions {
  position: absolute;

  right: 10px;

  top: 10px;
}

.jweet__actions span {
  cursor: pointer;
}

.jweet__actions span:first-child {
  margin-right: 10px;
}

/* Auth */

.authContainer {
  display: flex;

  flex-direction: column;

  height: 100vh;

  justify-content: center;

  align-items: center;
}

.main-title {
  margin: 10px 0 0 10px;
  margin-bottom: 30px;
  font-size: 15px;
  line-height: 30px;
  font-weight: bolder;
  color: #393053;
}

.auth-title {
  margin: 0px 0 0 0px;
  margin-bottom: 10px;
  font-size: 13px;
  line-height: 30px;
  font-weight: bolder;
  color: #393053;
}

.authBtns {
  display: flex;

  justify-content: space-between;

  width: 100%;

  max-width: 320px;
}

.authBtn {
  cursor: pointer;

  border-radius: 20px;

  border: none;

  padding: 5px 0px;

  font-size: 12px;

  text-align: center;

  width: 150px;

  background: #3e54ac;

  cursor: pointer;
}

/* AuthForm */

.authInput {
  max-width: 320px;

  width: 100%;

  padding: 10px;

  border-radius: 30px;

  background-color: rgba(255, 255, 255, 1);

  margin-bottom: 10px;

  font-size: 12px;

  color: black;
}

.authSubmit {
  text-align: center;

  background: #3e54ac;

  color: white;

  margin-top: 10px;

  cursor: pointer;
}

.authError {
  color: tomato;

  text-align: center;

  font-weight: 500;

  font-size: 12px;
}

